import React, { useContext } from 'react'
import Link from '@material-ui/core/Link'
import { makeStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import Title from './Title'
import { Appcontext } from '../AppContext'

interface IProps {
  type: number
}

function preventDefault(event: any) {
  event.preventDefault()
}

const useStyles = makeStyles({
  depositContext: {
    flex: 1,
  },
})

export default function Totals(props: IProps) {
  const classes = useStyles()
  const appContext = useContext(Appcontext)
  let total: number | undefined = 0
  let title = ''
  let delta = 0
  switch (props.type) {
    case 1:
      total = appContext?.total
      title = 'Total Confirmed Cases'
      delta = appContext?.deltaconfirmed
      break
    case 2:
      total = appContext?.totalActive
      title = 'Total Active Cases'
      delta = -1 //appContext?.deltaactive
      break
    case 3:
      total = appContext?.totalrecovered
      title = 'Total Recovered Cases'
      delta = appContext?.deltarecovered
      break
    case 4:
      total = appContext?.totaldeceased
      title = 'Total Deceased'
      delta = appContext?.deltadeceased
      break
  }

  return (
    <React.Fragment>
      <Title>{title}</Title>
      <Typography component="p" variant="h4">
        {total}
      </Typography>
      <Typography color="textSecondary" className={classes.depositContext}>
        on {new Date().toDateString()}
      </Typography>
      {delta > -1 ? (
        <Typography color="textSecondary" className={classes.depositContext}>
          +[{delta}]
        </Typography>
      ) : null}
    </React.Fragment>
  )
}
