import React from 'react'
import './App.css'
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles'
import Main from './Main'

const darkTheme = createMuiTheme({
  palette: {
    type: 'light',
    primary: {
      light: '#757ce8',
      main: '#3f5776',
      dark: '#002877',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44776',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
})

function App() {
  return (
    <ThemeProvider theme={darkTheme}>
      <div className="App">
        <Main></Main>
      </div>
    </ThemeProvider>
  )
}

export default App
