import React, { useContext } from 'react'
import { useTheme } from '@material-ui/core/styles'
import {
  BarChart,
  Bar,
  Cell,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  LabelList,
  ResponsiveContainer,
} from 'recharts'
import Title from './Title'
import { Appcontext } from '../AppContext'

const renderCustomizedLabel = (props) => {
  const { x, y, width, height, value } = props
  const radius = 10

  return (
    <g>
      <circle cx={x + width / 2} cy={y - radius} r={radius} fill="#fff" />
      <text
        x={x + width / 2}
        y={y - radius}
        fill="#3f5776"
        textAnchor="middle"
        dominantBaseline="middle"
      >
        {value}
      </text>
    </g>
  )
}

export default function StateChart() {
  const theme = useTheme()
  const appContext = useContext(Appcontext)
  const avg = '0.0'
  return (
    <React.Fragment>
      <Title>
        {`India Daily Cases`} {avg}
      </Title>
      <ResponsiveContainer width="100%" height={400}>
        <BarChart
          data={appContext?.statewise}
          height={400}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="date" />
          <YAxis domain={[0, 'dataMax + 100']} />
          <Tooltip />
          <Legend />
          <Bar dataKey="count" fill="#8884d8" minPointSize={0}>
            <LabelList dataKey="count" content={renderCustomizedLabel} />
          </Bar>
        </BarChart>
      </ResponsiveContainer>
    </React.Fragment>
  )
}
